import React, { useEffect } from "react"
import { MyUI } from "src/core/components"
import { HConfig } from "src/core/helpers"
import styled from "styled-components"
const Form = styled.form`
  border-radius: 10px;
  text-align: left !important;
  // box-shadow: none !important;
  background-color: #fff;
`

const titlePage = "Verify 2FA"
function Check2FA(props) {
  const { resToken, dispatchLoginSuccess } = props || {
    resToken: {},
    dispatchLoginSuccess: () => {},
  }
  useEffect(() => {
    HConfig.changeTitle(titlePage, titlePage)
    return () => {
      HConfig.changeTitle(null, "")
    }
  }, [])
  return (
    <div className="container">
      <div className="min-vh-100 row">
        <div className="col-md-5 m-auto">
          {resToken.fa2_needenable === true ? (
            <Form
              className="p-5 rounded-sm shadow text-center"
              onSubmit={e => {
                e.preventDefault()
              }}
            >
              <MyUI
                type="setting2fa_generation2fa"
                fnList={{
                  dispatchLoginSuccess: dispatchLoginSuccess,
                }}
              />
            </Form>
          ) : resToken.fa2_needverify === true ? (
            <Form
              className="p-5 rounded-sm shadow text-center"
              onSubmit={e => {
                e.preventDefault()
              }}
            >
              <MyUI
                type="setting2fa_verify2fa"
                fnList={{
                  dispatchLoginSuccess: dispatchLoginSuccess,
                }}
              />
            </Form>
          ) : (
            <div></div>
          )}
        </div>
      </div>
    </div>
  )
}

export default Check2FA
